import React, { Component } from 'react'

export default class FadedHeader extends Component <{slide: string, fade: string, title: string},{}>{
    
    render() {
        return (
            <header data-aos={"fade-"+this.props.fade} className={"slide-header-"+this.props.slide}>
                {this.props.title}
            </header>
        )
    }
}
