import React, { Component } from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Card from 'react-bootstrap/Card'
import Container from 'react-bootstrap/Container';

export default class ContactUs extends Component {

  private mailFields: any = {};

  updateFields() {
    let formInputs = document.getElementsByClassName('form-control') as HTMLCollectionOf<HTMLInputElement>;
    this.mailFields.email = formInputs[0].value;
    this.mailFields.subject = formInputs[1].value;
    this.mailFields.body = formInputs[2].value;
    console.log(this.mailFields);
  }

  sendEmail() {
    window.location.href = `mailto:concepttocloud@gmail.com?subject=${this.mailFields.subject}&body=${this.mailFields.body}&cc=${this.mailFields.email}`;
  }

  render() {
    return (
      <div className="App">
        
        <header className="Contact-header">
            <h1 className="contact">Contact Us</h1>
        </header>

        <Row>

          <Col md={{span:5, offset: 1}} xs={{span:10, offset: 1}}>
            <Form onChange = {() => this.updateFields()}>
              <Form.Group controlId="contactForm.email">
                <Form.Label>Your contact email:</Form.Label>
                <Form.Control type="email" placeholder="name@example.com" />
              </Form.Group>
              <Form.Group controlId="contactForm.subject">
                <Form.Label>I am looking to:</Form.Label>
                <Form.Control as="select">
                  <option>Book a consultation</option>
                  <option>Make an update to my site</option>
                  <option>Change my plan</option>
                </Form.Control>
              </Form.Group>
              <Form.Group controlId="contactForm.body">
                <Form.Label>Provide some detail and we'll get back to you shortly:</Form.Label>
                <Form.Control as="textarea" />
              </Form.Group>
              <Button variant="info" onClick={() => this.sendEmail()}>Submit</Button>
            </Form>
          </Col>

        <Col md={5}>
          <Container>
                <u><h1>How Can We Help? </h1></u>
                {/* <Card>
                    <Card.Header as="h5">Get Inspired</Card.Header>
                    <Card.Body>
                      <Card.Title>Examples to Get Started </Card.Title>
                      <Card.Text>
                        Feel free to browse through template that have already been created to spark the UI/UX process.
                      </Card.Text>
                      <Button variant="info" href='/templates'>View Templates</Button>
                    </Card.Body>
                  </Card> */}

                  <Card>
                    <Card.Header as="h5">Explore our Products</Card.Header>
                    <Card.Body>
                      <Card.Title>Pick the Tools that Suit your needs</Card.Title>
                      <Card.Text>
                        Pay for what you need and not the extras.
                        We offer an array of products and services that can help build a business from the ground up or fit in to your current workflow.
                      </Card.Text>
                      <Button variant="info" href='/#/templates'>Our Products</Button>
                    </Card.Body>
                  </Card>
                    </Container>
        </Col>

      </Row>

      </div>

    );
  }
}
