import React, { Component } from 'react'
import Jumbotron from 'react-bootstrap/Jumbotron'
import Container from 'react-bootstrap/Container'
import Slide from '@material-ui/core/Slide';
import Button from 'react-bootstrap/Button';

// Defines the datatype for packageDescription object
type Dictionary = { [index: string]: string }

// Using typescript, when you extend Component, you specify <props, state> within angle brackets
// https://stackoverflow.com/questions/47561848/property-value-does-not-exist-on-type-readonly
export default class PackageJumbotron extends Component<{title: string, productId: string}, {isVisible: boolean, productId: string, title: string}> {

    private static packageDescription: Dictionary = {
        default: 'Default package description. Should never be visible, in theory',
        oneTime: 'Initial site creation, no support moving forward',
        annual: 'Initial site creation, monthly charge for 24/7 support',
        payAsYouGo: 'Initial site creation, a fee charged for each alteration moving forward'
    }

    constructor(props: any) {
        super(props);
        this.state = {
            title: props.title,
            productId: props.productId,
            isVisible: true
        };
    }

    setVisibility(visibility: boolean) {
        this.setState({isVisible: visibility});
    }

    componentDidUpdate(previousProps: any, previousState: any) {
        if (this.props.productId !== previousProps.productId
            || !previousState.isVisible) {
            this.setState({
                title: this.props.title,
                productId: this.props.productId,
                isVisible: true
            });
        }
    } 

    render() {
        return (
            <Slide direction='right' in={this.state.isVisible} timeout={1000} mountOnEnter unmountOnExit>
                <Jumbotron fluid className="c2cWhite">
                    <Container>
                        <h2>{this.state.title}</h2>
                        <div>{PackageJumbotron.packageDescription[this.state.productId]}</div>
                    </Container>
                    <hr ></hr>
                    <Button className='c2cIndigo' onClick={() => this.setVisibility(false)}>Hide</Button>
                </Jumbotron>
            </Slide>
        )
    }
}
